import { MailAttachment, MailMessage } from './types';

export interface MailClientInterface {
  readMailbox(
    mailbox: string,
    lastSeenTimestamp?: number,
    lastSeenId?: string,
    limit?: number,
  ): Promise<Array<MailMessage>>;

  reply(
    messageId: string,
    from: string,
    body: string,
    concept?: boolean,
  ): Promise<void>;

  getAttachments(
    mailbox: string,
    messageId: string,
  ): Promise<Array<MailAttachment>>;

  addCategory(messageId: string, ...category: Array<string>): Promise<void>;

  removeCategory(messageId: string, ...category: Array<string>): Promise<void>;
}
